<template>
    <div id='About' v-if="isShow">
        <div class="header">
            <div class='top' @click="$router.push({
                name:'pic',
                params:{
                    pic_list:pic_list,
                    SerialID:detail.SerialID
                }
            })">
                <img :src="detail.CoverPhoto | pic_URL" alt="">
                <p>{{pic_count}}张照片</p>
            </div>
            <div class="bottom">
                <div>
                    <p>{{detail.market_attribute.dealer_price}}</p>
                    <p>指导价{{detail.market_attribute.official_refer_price}}</p>
                </div>
                <button>{{detail.BottomEntranceTitle}}</button>
            </div>
        </div>
        <div class="body">
            <div class="select_year">
                <p 
                    v-for="(it,i) in this.list"
                    @click='index = i'
                    :style="{color:index === i ? '#60adf8' : 'unset'}"
                    :key='i'>{{i === 'all' ? '全部' : i}}</p>
            </div>
            <ul class="list" ref='list'>
                <li v-for="(it,i) in list[index]" :key="i">
                    <p>
                        <span>{{it.car_id}}</span>
                        <span>{{it.exhaust_str}}</span>
                        <span>{{it.max_power_str}}</span>
                        <span>{{it.inhale_type}}</span>
                    </p>
                    <div>
                        <p>
                            <span>{{it.market_attribute.year}}款</span>
                            <span>{{it.exhaust}}T</span>
                            <span>{{it.trans_type}}T</span>
                            <span>{{it.car_name}}T</span>
                        </p>
                        <p>
                            <span>{{it.max_power}}马力</span>
                            <span>{{it.gear_num}}档</span>
                        </p>
                        <p>
                            <span>指导价{{it.market_attribute.dealer_price_max}}</span>
                            <span>{{it.market_attribute.dealer_price_min}}起</span>
                        </p>
                    </div>
                    <button @click='ask_price(it)'>{{detail.BottomEntranceTitle}}</button>
                </li>
            </ul>
        </div>
        <div class='about_bottom'>
            <button>
                <p>{{detail.BottomEntranceTitle}}</p>
                <p>本地经销商问你报价</p>
            </button>
        </div>
        <router-view />
    </div>
    <Loading v-else/>
</template>
<script>
export default {
    name:'About',
    data(){
        return{
            detail:new Object(),
            isShow:false,
            pic_list:'',
            list:new Object(),
            index:'all',
            city:''
        }
    },
    async created(){
        await this.axios.get('/car_api/v2-car-getInfoAndListById.html?SerialID=' + this.$route.params.id)
        .then(res => {
            if(res.status ===200 && res.data.code === 1){
                this.isShow = true;
                this.detail = res.data.data;
                this.get_tab(res.data.data.list);
            }
        })
        this.axios.get('/car_api/v2-car-getImageList.html?SerialID='+this.detail.SerialID)
        .then(res => {
            if(res.status ===200 && res.data.code === 1){
                this.pic_list = res.data.data;
            }
        })
        this.axios.get('/car_api/location-client.html?')
        .then(res => {
            if(res.status ===200 && res.data.code === 1){
                this.city = res.data.data;
            }
        });
    },
    computed:{
        pic_count(){
            let count = 0;
            for (const obj of this.pic_list) {
                count += obj.List.length;
            }
            return count;
        }
    },
    methods:{
        get_tab(it){
            this.list['all'] = it;
            it.forEach(e => {
                if(this.list[e.market_attribute.year]){
                    this.list[e.market_attribute.year].push(e);
                }else{
                    this.list[e.market_attribute.year] = new Array();
                    this.list[e.market_attribute.year].push(e);
                }
            });
        },
        ask_price(info){
            this.$router.push({
                name:'ask',
                query:{
                    carId:info.car_id,
                    city:this.city
                }
            })
        }
    }
}
</script>
<style lang='stylus' scoped>
    #About
        position absolute
        top 0
        right 0
        background-color white
        transition all .3s ease-out
        height 100%
        overflow hidden
        width 100%
        background-color #f4f4f4
        .header
            position relative
            height 300px
            width 100%
            background-color white
            .top
                position relative
                height 240px
                img 
                    height 100%
                    width 100%
                    background-size cover
                p
                    position absolute
                    bottom 12px
                    right 12px
                    padding 6px 10px
                    font-size 14px
                    background-color rgba(0,0,0,.5)
                    color white
                    border-radius 20px
            .bottom
                height 60px
                display flex
                justify-content space-between
                padding 0 12px
                align-items center
                >div
                    width 160px
                    height 40px
                    p:first-child
                        color #ea3223
                        font-size 20px
                    p:last-child
                        color #bbb
                        margin-top 8px
                        font-size 14px
                button 
                    width 160px
                    height 40px
                    background-color  #4daef8
                    color white
                    border 0
                    border-radius 4px
        .body
            position relative
            width 100%
            height 100%
            margin-top 10px
            .select_year
                height 50px
                width 100%
                overflow auto
                padding 0 12px
                display flex
                align-items center
                background-color white
                p
                    margin 0 12px
                    font-size 18px
            .list
                position relative
                height 50%
                margin-bottom 50px
                overflow auto
                li
                    width 100%
                    p
                        padding 4px 6px
                        color #666
                    >div
                        background-color white
                        overflow hidden
                        p:first-child
                            font-size 16px
                            color #777
                            margin-bottom 10px
                        p:nth-child(2)
                            font-size 14px
                            color #bbb
                        p:last-child
                            font-size 16px
                            float right
                            margin-bottom 10px
                            span:last-child
                                color #ea3223
                        span 
                            margin 0 4px
                        span:first-child
                            margin-left 0
                    button 
                        background-color white
                        width 100%
                        height 50px
                        font-size 16px
                        color #5babf8
                        border 0
                        margin-top 1px
        .about_bottom
            position absolute
            bottom 0
            width 100%
            height 50px
            button 
                width 100%
                height 100%
                background-color #5babf8
                color white
                border 0
                p
                    font-size 16px
                    &:last-child
                        font-size 12px
                        margin-top 2px
</style>